import './App.css';
import {useState} from "react";

import likeLogo from './assets/img/ico_like.png';
import {logDOM} from "@testing-library/react";

function App() {
    let [title, setTitle] = useState(['자바스크립트', '리액트', '스벨트']);
    let [like, setLike] = useState([0, 0, 0]);
    let [modal, setModal] = useState(false);
    let [index,setIndex] = useState(0);
    let [inputV, setInputV] = useState();
    let [day, setDay] = useState(['2020년 11월 28일', '2020년 10월 20일', '2020년 9월 10일']);

    const now = new Date();
    const year = now.getFullYear();
    const month = now.getMonth() + 1;
    const date = now.getDate();
    let today = year + '년' + month + '월' + date + '일';
    console.log(today);


    return (
        <div className="wrap">
            <div className="header">
                <p className="logo">ReactBlog</p>
            </div>

            {
                title.map((v, i) => {
                    return (
                        <div className="list" key={i}>
                            <div className="title_wrap">
                                    <div className="left" onClick={ () => {
                                        setModal(true);
                                        setIndex(i)
                                    }}>
                                    <h4 className="title">{title[i]}</h4>
                                    <span onClick={ (e) => {
                                        e.stopPropagation();
                                        let copy = [...like];
                                        copy[i]++
                                        setLike(copy)
                                    } }><img src={likeLogo} /><span>{like[i]}</span>️</span>
                                    <button type='button' className='btn_delete' onClick={ (e) => {
                                        e.stopPropagation();
                                        let copy = [...title];
                                        copy.splice(i, 1);
                                        setTitle(copy);
                                    }}>삭제</button>
                                </div>
                                <strong>{day[i]}</strong>
                            </div>
                        </div>
                    )
                })
            }

            {
                modal == true? <Modal title = {title} index = {index} day = {day} today = {today} /> : null
            }

            <div className="btn" >
                <input placeholder='제목을 작성해주세요' onChange={ (e) => {
                    setInputV(e.target.value);
                    console.log(e.target.value);
                }}></input>
                <button onClick={ (props) => {
                    let titleCopy = [...title];
                    titleCopy.unshift(inputV);
                    inputV == null? alert('제목을 입력하세요') : setTitle(titleCopy);

                    let dateCopy = [...day];
                    dateCopy.unshift(today);
                    setDay(dateCopy);

                    let likeCopy = [...like];
                    likeCopy.unshift(0);
                    setLike(likeCopy);

                } }>발행하기</button>
            </div>
        </div>
    );
}

const Modal = (props) => {
    return(
        <div className="modal">
            <h4>{props.title[props.index]}</h4>
            <p className="date">{props.day[props.index]}</p>
            <p className="cnt">Lorem ipsum dolor sit amet, consectetur adipisicing elit. A culpa cum dignissimos eos explicabo inventore modi officiis tempora ullam veniam! Adipisci distinctio dolorem nisi odio, quia quisquam quod sit veniam.</p>
        </div>
    )
}

export default App;
